@media screen and (min-width: 768px) {
  .contacts_buttons {
    display: flex;
    gap: 16px;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (max-width: 767px) {
  .btn_big {
    margin-bottom: 16px;
  }
}
